import { Component } from '@angular/core';
import { BeforeSlideDetail, InitDetail } from 'lightgallery/lg-events';
import lgZoom from 'lightgallery/plugins/zoom';
import * as moment from 'moment-timezone';
import 'flipclock';
// import { JQueryStatic } from 'jquery/JQueryStatic';

declare const $;

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'wedding';

  settings = {
    counter: false,
    plugins: [lgZoom],
    thumbnail: true
  };
  onBeforeSlide = (detail: BeforeSlideDetail): void => {
    const { index, prevIndex } = detail;
  };

//   onInit = (detail: InitDetail): void => {
//     detail.instance.openGallery();
// };
ngAfterViewInit(): void {
  let clock: any;

  // Grab the current date
  const currentDate = new Date();

  // Target future date/24 hour time/Timezone
  const targetDate = moment.tz("2024-07-13 19:00", "Asia/Ho_Chi_Minh");

  // Calculate the difference in seconds between the future and current date
  const diff = targetDate.valueOf() / 1000 - currentDate.getTime() / 1000;

  if (diff <= 0) {
    // If remaining countdown is 0
    clock = $(".clock").FlipClock(0, {
      clockFace: "DailyCounter",
      countdown: true,
      autostart: false
    });
    console.log("Date has already passed!");
  } else {
    // Run countdown timer
    clock = $(".clock").FlipClock(diff, {
      clockFace: "DailyCounter",
      countdown: true,
      callbacks: {
        stop: function() {
          console.log("Timer has ended!");
        }
      }
    });

    // Check when timer reaches 0, then stop at 0
    this.checkTime(clock);
  }
}


checkTime(clock: any) {
  const t = clock.getTime();
  if (t <= 0) {
    clock.setTime(0);
  } else {
    setTimeout(() => {
      this.checkTime(clock);
    }, 1000);
  }
}
}
